import styled from 'styled-components';

export const Container = styled.a`
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    flex-direction:column;
    background: #fff;
    padding: 7px;
    width: 285px;
    height: 336px;
    text-decoration: none;

    .title{
        margin-bottom: 5px;
        font-size: 12px;
        color: #5e5e5e;
        font-family: Muli, Arial;
    }
    .price{
        margin-bottom: 1px;
        font-size: 16px;
        color: #383838;
        font-family: Muli, Arial;
    }

    .vezes{
        font-size: 14px;
        color: #5e5e5e;
        font-family: Muli, Arial;
    }
`;