import React from "react";

import { Container } from "./styles";

const ProductCard = ({ url, image, title, price }) => { 

    const proxyPrice = parseInt(price.substring(3).replace(",","."))/10;

    const priceSub = proxyPrice;

    return(
        <Container href={url}>
            <img src={image} className="image" alt="Imagem do Sapato"/>
            <span className="title">{title}</span>
            <span className="price">{price}</span>
            <span className="vezes">10 vezes de {priceSub}</span>
        </Container>
    );
};

export default ProductCard;
